import App from '@/App';
export default class BuildersToolDataEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('builderstool/setBuildHeight', data.buildHeight);
        App.interfaceManager.container.$store.commit('builderstool/setState', data.state);
        App.interfaceManager.container.$store.commit('builderstool/setRotation', data.rotation);
        App.interfaceManager.container.$store.commit('builderstool/setMinHeight', data.minHeight);
        App.interfaceManager.container.$store.commit('builderstool/setMaxHeight', data.maxHeight);
        App.interfaceManager.container.$store.commit('builderstool/setBuildHeightOn', data.buildHeightOn);
        App.interfaceManager.container.$store.commit('builderstool/setStateOn', data.stateOn);
        App.interfaceManager.container.$store.commit('builderstool/setRotationOn', data.rotationOn);
        App.interfaceManager.container.$store.commit('builderstool/setFreeze', data.freeze);
        App.interfaceManager.container.$store.commit('builderstool/setHideWired', data.hideWired);
        App.interfaceManager.bus.$emit("updateValues");
    }
}
