import Vue from 'vue';
import Vuex from 'vuex';
import { session } from './modules/session';
import { commands } from './modules/commands';
import { jukebox } from './modules/jukebox';
import { slotmachine } from './modules/slotmachine';
import { youtubeplayer } from './modules/youtubeplayer';
import { twitchplayer } from './modules/twitchplayer';
import { furniture } from './modules/furniture';
import { alert } from './modules/alert';
import { useralert } from './modules/useralert';
import { builderstool } from './modules/builderstool';
Vue.use(Vuex);
const store = {
    state: {
        connected: false
    },
    modules: {
        session,
        commands,
        jukebox,
        slotmachine,
        youtubeplayer,
        twitchplayer,
        furniture,
        alert,
        useralert,
        builderstool
    },
    mutations: {
        setConnected(state, val) {
            state.connected = val;
        }
    }
};
export default new Vuex.Store(store);
