const state = {
    image: "",
    title: "",
    text: "",
    link: "",
    roomId: 0,
    username: "",
    open: false,
};
const namespaced = true;
const mutations = {
    setImage(state, image) {
        state.image = image;
    },
    setTitle(state, title) {
        state.title = title;
    },
    setText(state, text) {
        state.text = text;
    },
    setLink(state, link) {
        state.link = link;
    },
    setRoomId(state, roomId) {
        state.roomId = roomId;
    },
    setUsername(state, username) {
        state.username = username;
    },
    setOpen(state, open) {
        state.open = open;
    },
};
export const useralert = {
    namespaced,
    state,
    mutations
};
