import App from '@/App';
export default class ShowAlertEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('useralert/setImage', data.image);
        App.interfaceManager.container.$store.commit('useralert/setTitle', data.title);
        App.interfaceManager.container.$store.commit('useralert/setText', data.text);
        App.interfaceManager.container.$store.commit('useralert/setLink', data.link);
        App.interfaceManager.container.$store.commit('useralert/setRoomId', data.roomId);
        App.interfaceManager.container.$store.commit('useralert/setUsername', data.username);
        App.interfaceManager.bus.$emit("setAlertTime", data.time);
        App.interfaceManager.container.$store.commit('useralert/setOpen', true);
        if (data.roomId != 0) {
            var sound = new Audio("/assets/event.mp3");
            sound.volume = 0.2;
            sound.play();
        }
        else if (data.link !== '') {
            var sound = new Audio("/assets/hal.mp3");
            sound.volume = 0.2;
            sound.play();
        }
        else {
            var sound = new Audio("/assets/ha.mp3");
            sound.volume = 0.2;
            sound.play();
        }
    }
}
