const state = {
    open: false,
    buttonOpen: false,
    buildHeight: 0,
    state: 0,
    rotation: 0,
    minHeight: 0,
    maxHeight: 0,
    buildHeightOn: false,
    stateOn: false,
    rotationOn: false,
    freeze: false,
    hideWired: false,
    furniCount: 0
};
const namespaced = true;
const mutations = {
    setBuildHeight(state, buildHeight) {
        state.buildHeight = buildHeight;
    },
    setState(state, newState) {
        state.state = newState;
    },
    setRotation(state, newRotation) {
        state.rotation = newRotation;
    },
    setMinHeight(state, newMinHeight) {
        state.minHeight = newMinHeight;
    },
    setMaxHeight(state, newMaxHeight) {
        state.maxHeight = newMaxHeight;
    },
    setBuildHeightOn(state, buildHeightOn) {
        state.buildHeightOn = buildHeightOn;
    },
    setStateOn(state, stateOn) {
        state.stateOn = stateOn;
    },
    setRotationOn(state, rotationOn) {
        state.rotationOn = rotationOn;
    },
    setFreeze(state, freeze) {
        state.freeze = freeze;
    },
    setHideWired(state, hideWired) {
        state.hideWired = hideWired;
    },
    setFurniCount(state, furniCount) {
        state.furniCount = furniCount;
    },
    setOpen(state, open) {
        state.open = open;
    },
    setButtonOpen(state, buttonOpen) {
        state.buttonOpen = buttonOpen;
    }
};
export const builderstool = {
    namespaced,
    state,
    mutations
};
